import React, { useState } from 'react';
import { Table } from './table';
import { candidateColumns } from '@/lib/definitions';

const CandidateTable = ({ candidateTableData, candidateTableColumns} :{candidateTableData:any,candidateTableColumns:candidateColumns}) => {
  const [currentPage, setCurrentPage] = useState(1);
  const totalPages = Math.ceil(candidateTableData.content.length / 10); // Assuming 10 items per page

  const handlePageChange = (newPage: number) => {
    setCurrentPage(newPage);
  };

  const paginatedData = candidateTableData.content.
  slice(
    (currentPage - 1) * 10,
    currentPage * 10
  );

  return (
    <Table
      data={paginatedData}
      columns={candidateTableColumns}
      getRowLink={(item, index) => `/candidates/${item.contactId}`}
      currentPage={currentPage}
      totalPages={totalPages}
      onPageChange={handlePageChange}
      pageSize={10}
    />
  );
};

export default CandidateTable;
