import Link from 'next/link';
import React from 'react';

// Define types for the Table component props
interface Column {
  Header: string;
  accessor: string;
  hiddenOnSmall?: boolean;
}

interface TableProps {
  columns: Column[];
  data: any[];
  getRowLink?: (item: any, index: number) => string; // Optional link generator
  currentPage: number;
  totalPages: number;
  onPageChange: (page: number) => void;
  pageSize: number;
}

export const Table: React.FC<TableProps> = ({
  columns,
  data,
  getRowLink,
  currentPage,
  totalPages,
  onPageChange,
  pageSize,
}) => {
  // Log data for debugging
  console.log(data);

  // Handle empty data or columns
  if (!data || data.length === 0) {
    return <div className="text-center text-gray-500 dark:text-gray-400 py-4">No data available</div>;
  }

  if (!columns || columns.length === 0) {
    return <div className="text-center text-gray-500 dark:text-gray-400 py-4">No columns defined</div>;
  }

  return (
    <div className="rounded-lg border border-gray-200 bg-white shadow-lg dark:border-gray-700 dark:bg-gray-800 overflow-hidden">
      <div className="flex flex-col">
        {/* Table Header */}
        <div
          className="grid grid-cols-3 rounded-t-lg bg-gradient-to-r from-blue-50 to-purple-50 dark:from-gray-700 dark:to-gray-700 sm:grid-cols-5"
          role="rowheader"
        >
          {columns.map((col, index) => (
            <div
              key={index}
              className={`p-3 xl:p-4 ${
                col.hiddenOnSmall ? 'hidden sm:block' : ''
              }`}
            >
              <h5 className="text-sm font-semibold uppercase tracking-wide text-gray-700 dark:text-gray-200">
                {col.Header}
              </h5>
            </div>
          ))}
        </div>

        {/* Table Rows */}
        {data.map((item, index) => {
          const rowContent = (
            <div
              className={`grid grid-cols-3 sm:grid-cols-5 transition-all duration-200 ease-in-out ${
                getRowLink ? 'hover:bg-gray-50 dark:hover:bg-gray-700 cursor-pointer' : ''
              } border-b border-gray-100 dark:border-gray-700`}
              key={index}
              role="row"
            >
              {columns.map((col, colIndex) => (
                <div
                  key={colIndex}
                  className={`p-3 xl:p-4 ${
                    col.hiddenOnSmall ? 'hidden sm:block' : ''
                  }`}
                >
                  <p
                    className={`text-md font-light ${
                      item[col.accessor] === false
                        ? 'bg-red-100 text-red-800 px-2 py-1 rounded-sm font-semibold text-base w-fit border border-black dark:border-white'
                        : item[col.accessor] === true
                        ? 'bg-green-500 text-white px-2 py-1 rounded-sm font-semibold text-sm w-fit border border-black dark:border-white'
                        : 'text-gray-700 dark:text-gray-200'
                    }`}
                  >
                    {item[col.accessor]==true? "Active" : item[col.accessor]==false? "Inactive" : item[col.accessor]}
                  </p>
                </div>
              ))}
            </div>
          );

          // If a link generator is provided, wrap the row in a Link component
          return getRowLink ? (
            <Link href={getRowLink(item, index)} key={index}>
              {rowContent}
            </Link>
          ) : (
            rowContent
          );
        })}
      </div>

      {/* Pagination Controls */}
      <div className="flex justify-between items-center p-4 bg-gray-50 dark:bg-gray-700">
        <button
          onClick={() => onPageChange(currentPage - 1)}
          disabled={currentPage === 1}
          className="px-4 py-2 text-sm font-medium text-gray-700 bg-white border border-gray-300 rounded-lg hover:bg-gray-100 dark:bg-gray-800 dark:text-gray-200 dark:border-gray-600 dark:hover:bg-gray-700 disabled:opacity-50 disabled:cursor-not-allowed"
          aria-label="Previous Page"
        >
          Previous
        </button>
        <span className="text-sm text-gray-700 dark:text-gray-200">
          Page {currentPage} of {totalPages}
        </span>
        <button
          onClick={() => onPageChange(currentPage + 1)}
          disabled={currentPage === totalPages}
          className="px-4 py-2 text-sm font-medium text-gray-700 bg-white border border-gray-300 rounded-lg hover:bg-gray-100 dark:bg-gray-800 dark:text-gray-200 dark:border-gray-600 dark:hover:bg-gray-700 disabled:opacity-50 disabled:cursor-not-allowed"
          aria-label="Next Page"
        >
          Next
        </button>
      </div>
    </div>
  );
};